export class FileHttpDownFileViewModel {
  /// <summary>
  ///  匯出序號
  /// <summary>
  public export_seq: number;
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  轉檔群組
  /// <summary>
  public exec_group: string;

  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;

  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  重複執行註記
  /// <summary>
  public can_rerun: boolean;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  備註
  /// <summary>
  public note: string;

  /// <summary>
  ///  執行失敗停止
  /// </summary>
  public is_fail_stop: boolean;

  /// <summary>
  ///  匯出檔案路徑主鍵
  /// </summary>

  public out_file_path_key: string;

  /// <summary>
  ///  匯出檔案路徑
  /// </summary>

  public out_file_path: string;



  /// <summary>
  ///  啟用註記說明
  /// <summary>
  public is_active_desc: string;
  /// <summary>
  ///  啟用註記顏色
  /// <summary>
  public is_active_color: string;
  /// <summary>
  ///  啟用顏色
  /// <summary>
  public is_active_color_right: string;

  /// <summary>
  ///  功能名稱
  /// <summary>
  public function_name: string;
  // <summary>
  ///  遠端連線編號
  /// <summary>
  public remote_conn_id: string;

  /// <summary>
  ///  檔案名稱
  /// <summary>
  public filename: string;

  /// <summary>
  ///  下載類型主鍵(參考共用代碼：061|1,061|3,061|6,061|7)
  /// <summary>
  public down_type_key: string;
  /// <summary>
  ///  下載類型
  /// <summary>
  public down_type: string;
/// <summary>
  ///  檔案內文格式主鍵
  /// <summary>
  public file_format_key: string;
  /// <summary>
  ///  檔案內文格式
  /// <summary>
  public file_format: string;
  /// <summary>
  ///  url
  /// <summary>
  public url: string;
  /// <summary>
  ///  連線字串
  /// <summary>
  public conn_string: string;
  /// <summary>
  ///  功能參數
  /// <summary>
  public xpath: string;
 /// <summary>
  ///  使用cookie
  /// <summary>
  public cookie: boolean;

}



