import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileHttpDownFileListResponse } from "../../Model/Response/FileHttpDownFileListResponse";
import { FileHttpDownFileResponse } from "../../Model/Response/FileHttpDownFileResponse";
import { FileHttpDownFileViewModel } from '../../Model/ViewModel/FileHttpDownFileViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileHttpDownFileServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileHttpDownFileServicesProvider extends FileServicesProvider {
  private FileHttpDownFiles: FileHttpDownFileListResponse;
  private FileHttpDownFile: FileHttpDownFileResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileHttpDownFile";
    console.log("Hello FileHttpDownFileServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileHttpDownFile?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileHttpDownFile/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileHttpDownFiles = new FileHttpDownFileListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileHttpDownFiles;
    });
  }



  public PostPutAsync(FileHttpDownFileViewModel:FileHttpDownFileViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileHttpDownFile";
    else
    sub_url = "FileHttpDownFile/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileHttpDownFileViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileHttpDownFileViewModel,mode).map(
      item => {
        this.FileHttpDownFile = new FileHttpDownFileResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileHttpDownFile;
      }
    );
  }
}



